.background {
	background: linear-gradient(-45deg, #ffffff, #b8eaff54, #ffffff, #93d7ff5b);
	background-size: 400% 400%;
	animation: gradient 30s ease infinite;
	height: 100vh;
  position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;

}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.noise{
    background-image: url(data:image/png;base64,);
    position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
    background-size: 5% 5%;
    opacity: 10%;

}